import { Component } from '@angular/core';
import { IonicPage, NavController, } from 'ionic-angular';
import { LanguageServiceProvider } from '../../providers/language-service/language-service';


@IonicPage()
@Component({
  selector: 'page-intro',
  templateUrl: 'intro.html',
})
export class IntroPage {
  year: number;

  constructor(
    public navCtrl: NavController,
    public langserv: LanguageServiceProvider) {
      const date = new Date();
      this.year = date.getFullYear();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IntroPage');
  }

  changelang(lang: string) {
    this.langserv.changelang(lang);
    // this.navCtrl.setRoot('HomePage');
    this.navCtrl.setRoot('HomePage');
    // this.app.getRootNavs()[0].setRoot('HomePage');
    // this.navCtrl.root = 'HomePage';
    // console.log(this.navCtrl.root = 'HomePage')
  }
  openUrl(url){
    window.open(url)
    // let parseUrl:any =  this.sanitizer.bypassSecurityTrustResourceUrl(url)
    // window.open(parseUrl);
  }

  onclickHome() {
    this.navCtrl.setRoot('HomePage');
  }

  openContact() {
    this.navCtrl.push('ContactPage');
  }

  openfeedback() {
    this.navCtrl.push('FeedbackPage');
  }
  openterms() {
    this.navCtrl.push('TermsPage');
  }

  openprivacy() {
    this.navCtrl.push('TermsPage');
  }
  
  checkLogin() {
    if (localStorage.getItem('userData')) {
      return true;
    }
    return false;
  }
}



